import * as React from "react";

class Timer {
  private timeoutId: number | null = null;
  private tickListeners: ((time: number) => void)[] = [];

  public isStarted = false;
  public time: number = 0;

  constructor() {
    this.loop = this.loop.bind(this);
  }

  private loop() {
    this.time = this.time + 1;
    this.tickListeners.forEach(i => i(this.time));
    this.timeoutId = window.setTimeout(this.loop, 1000);
  }

  private clearTimeout() {
    if (this.timeoutId) {
      window.clearTimeout(this.timeoutId);
    }
    this.timeoutId = null;
  }

  public start() {
    this.clearTimeout();
    this.loop();
    this.isStarted = true;
  }

  public stop() {
    this.clearTimeout();
    this.isStarted = false;
  }

  public onTick(callback: (time: number) => void): () => void {
    this.tickListeners.push(callback);

    return () => {
      this.tickListeners = this.tickListeners.filter(i => i !== callback);
    };
  }
}

export const timer = new Timer();

export function useTimer() {
  const [time, setTime] = React.useState(0);
  const handleTimerTick = React.useCallback(
    function(newTime: number) {
      setTime(newTime);
    },
    [time],
  );

  React.useEffect(() => {
    const handleTimerTickDisposer = timer.onTick(handleTimerTick);
    return () => handleTimerTickDisposer();
  }, [time]);

  return time;
}

timer.start();
