import * as React from 'react';
import { RouteComponentProps } from 'react-router-dom';

import { times } from 'utils';
import { timer, useTimer } from 'timer';

// icons
import {
  IconCross,
  IconPerson,
  IconUpDown,
  IconHat,
  IconAnalytics,
} from 'ui-kit/Icon';

import Card from './components/Card';

function Button(props: { slot?: string; children?: React.ReactNode }) {
  const { slot, children } = props;

  return (
    <button slot={slot} className="button">
      {children}
    </button>
  );
}

function CardWithTimer() {
  const time = useTimer();

  return (
    <Card>
      <Card.Slot name="address">Address</Card.Slot>
      <Card.Slot name="email">inom@space30.com</Card.Slot>
      <Card.Slot name="name">
        Inom
        <div>Mirdzhamolov</div>
        <div>Komilovich</div>
      </Card.Slot>
      <Card.Slot name="footer">
        <Button>Send email</Button>
      </Card.Slot>
      <Card.Slot>count: {time}</Card.Slot>
    </Card>
  );
}

export function ExampleSubcomponentStyle(
  props: RouteComponentProps<{ count: string }>,
) {
  const cardCount = Number(props.match.params.count) || 1;
  return (
    <div>
      {times(cardCount, index => (
        <CardWithTimer key={index} />
      ))}
    </div>
  );
}
